			</div>
		</div>
		<BackTop>
      <div class="top"><Icon type="chevron-up"></Icon></div>
    </BackTop>
    </div>
</template>

<script>
import headertop from '@/components/Header.vue'
import leftnav from '@/components/LeftNav.vue'
export default {
  name: 'app',
  components: {headertop, leftnav}
}
</script>
<style scoped>
    .top{
        background: rgba(0, 153, 229, .7);
        color: #fff;
        text-align: center;
    }
</style>


