<template>
	<div>
		<div class="top_bar">
			<div class="header clearfix">
				<div class="logimg">
					<a href="/" style="font-size:22px;line-height:46px">基于VUE的用户中心案例</a>
				</div>
				<div class="top_nav">
					<div class="user-avatar">
						<li>
							<a href="/">
								<Avatar size="large" src="https://inews.gtimg.com/newsapp_ls/0/1151158184_200200/0" style="background-color: #87d068" icon="person"></Avatar>
							</a>
						</li>
						<li>
							<Dropdown trigger="click" @on-click="turnUrl">
								<a href="javascript:void(0)">
									科技男
									<Icon type="arrow-down-b"></Icon>
								</a>
								<DropdownMenu slot="list" >
									<DropdownItem name='/user/info'>账号信息</DropdownItem>
									<DropdownItem name='/user/info/setting'>账号设置</DropdownItem>
									<DropdownItem divided name="/loginout">退出登录</DropdownItem>
								</DropdownMenu>
							</Dropdown>
						</li>
						<li>
							<Dropdown style="margin-left: 20px" @on-click="turnUrl">
						        <router-link to="/message/notice">消息通知</router-link>
						        <DropdownMenu slot="list">
						            <DropdownItem name='/message/notice'>通知</DropdownItem>
						            <DropdownItem name='/article/comment'>评论</DropdownItem>
						            <DropdownItem name='/message/subscribe'>关注</DropdownItem>
						        </DropdownMenu>
						    </Dropdown>
						</li>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import cookie from 'js-cookie'
export default {
  methods: {
    turnUrl (name) {
      if (name === '/loginout') {
        cookie.remove('user')
        this.$router.replace('/login')
      } else {
        this.$router.replace(name)
      }
    }
  }
}
</script>

<style>
	.head {
		height: 60px;
		border-bottom: 1px solid #ccc;
	}
</style>


