<template>
	<Menu  width="200" @on-select="turnUrl">
		<MenuItem name="/">
			<Icon type="ios-home" size="16"></Icon>
			首页
		</MenuItem>
		<MenuGroup :title="item.label" v-for="item in leftnav" :key="item.value">
			<MenuItem :name="child.value" v-for="child in item.children" :key="child.value">
				<Icon :type="child.icon" size="16"></Icon>
				{{child.label}}
			</MenuItem>
		</MenuGroup>
	</Menu>
</template>

<script>
 export default {
   data () {
     return {
       spinShow: false,
       spinShowloadmore: true,
       leftnav: [
         {
           value: '1',
           label: '创作',
           children: [
             {
               value: '/article/new',
               icon: 'document-text',
               label: '写作'
             }
           ]
         },
         {
           value: '2',
           label: '管理',
           children: [
             {
               value: '/article/own',
               icon: 'navicon-round',
               label: '内容管理'
             },
             {
               value: '/article/comment',
               icon: 'chatboxes',
               label: '评论管理'
             },
             {
               value: '/source/images',
               icon: 'images',
               label: '我的素材'
             },
             {
               value: '/fans/list',
               icon: 'ios-people',
               label: '粉丝分析'
             }
           ]
         },
         {
           value: '3',
           label: '数据',
           children: [
             {
               value: '/',
               icon: 'stats-bars',
               label: '图文统计'
             },
             {
               value: '/',
               icon: 'connection-bars',
               label: '订阅数统计'
             },
             {
               value: '/',
               icon: 'ios-analytics',
               label: '技术值'
             }
           ]
         },
         {
           value: '4',
           label: '收益',
           children: [
             {
               value: '/income/statisitcs',
               icon: 'ribbon-a',
               label: '收益统计'
             },
             {
               value: '/income/settlement',
               icon: 'ios-calendar',
               label: '结算中心'
             },
             {
               value: '/income/addaccountsback',
               icon: 'card',
               label: '收款账号'
             }
           ]
         },
         {
           value: '5',
           label: '设置',
           children: [
             {
               value: '/user/info',
               icon: 'gear-a',
               label: '账号设置'
             },
             {
               value: '/user/function',
               icon: 'heart-broken',
               label: '账号功能'
             },
             {
               value: '/user/userstatus',
               icon: 'ios-timer',
               label: '账号状态'
             }
           ]
         }
       ]
     }
   },
   methods: {
     turnUrl (name) {
       this.$router.replace(name)
     }
   }
 }
</script>


